nd-color:#6a7495}.brush-preview{border-radius:9999px;position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.brush-remove{position:absolute;color:#e53e3e;font-size:.875rem;top:-6px;right:0;-webkit-transition:opacity 70ms;transition:opacity 70ms;opacity:0}.checkered:hover .brush-remove{opacity:1}.pen-nib{font-size:1.125rem;color:#edf2f7;display:-webkit-box;display:flex;-webkit-box-pack:center;justify-content:center;-webkit-box-align:center;align-items:center;opacity:.8;width:30px;height:30px;text-align:center;cursor:pointer}.pen-nib:hover{opacity:1}.pen-nib-active{color:#68d391;outline-color:#fff;outline-style:dashed}
